@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');

.App {
  position: relative;
  text-align: center;
  width: 100vw;
  color: white;
  font-size: 2rem;
  background-image: linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.7)),url('bg.jpg');
  background-size: cover;
}

main {

  max-width: 80vw;
  margin: auto;

}

h1 {
  margin-top: 2vh;
  font-size: 4.5rem;
  font-family: 'Permanent Marker', cursive;
}
#author {
  font-size: 1rem;
}
@media (max-width: 500px){
  .App {
    width: 100vw;
    font-size: 1rem;
  }
  h1 {
    font-size: 2.5rem;
    margin-top: 0;
  }
}
