@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#quote-box{position:relative;margin-top:5vh;max-width:800px}.tweet-button{margin-left:5px}

.App{position:relative;text-align:center;width:100vw;color:white;font-size:2rem;background-image:linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.7)),url(/random-quote-machine/static/media/bg.ca110e08.jpg);background-size:cover}main{max-width:80vw;margin:auto}h1{margin-top:2vh;font-size:4.5rem;font-family:'Permanent Marker', cursive}#author{font-size:1rem}@media (max-width: 500px){.App{width:100vw;font-size:1rem}h1{font-size:2.5rem;margin-top:0}}

